'use client'

import { useGetExecutor } from "@/hooks/useGetExecutor"
import { Task } from "@/shared/types/task.types"

interface Props {
    task: Task
}

export function TabAuthorTasksInProcess({task}: Props) {
    
    const executor = useGetExecutor(task)
    
    return (
        <>
            <td className="border-r-1 border-gray-300 p-2">
                {task.priorityid.namepriority}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.statusid.namestatus}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.nametask}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.info}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.datecreate}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.datechange}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.dateestimatedcompletion}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {task.document}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {`${executor?.firstname} ${executor?.lastname}`}
            </td>
        </>
    )
}
